import React from "react";
import { injectIntl } from "react-intl";
import Flex from "../../../util/Flex";
import {
  Form,
  Button,
  Input,
  Space,
  message,
  Typography,
  Alert,
  Tabs,
  Spin,
  Card,
  Checkbox,
  Row,
  Col,
} from "antd";
import Context from "../../../Context";
import { withRouter } from "react-router-dom";
import { PlusOutlined, DeleteOutlined } from "@ant-design/icons";
import UserDetailTitle from "./UserDetailTitle";

const { Text } = Typography;

const { TabPane } = Tabs;

class UsersDetail extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      user: {},
      tabKey: "profile",

      interfaces: {},
      intrfacesActive: [],

      groups: {},
      groupsActive: [],

      ipAllow: [],
      ipDeny: [],
    };
  }

  pendingStart = () => {
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    this.setState({ pending: false });
  };

  componentDidMount() {
    this.load();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.userId !== this.props.userId) {
      this.load();
    }
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.load();
    }
  }

  load = async () => {
    this.pendingStart();
    let user = await this.context.api.q("/admin/user/get", {
      id: this.props.userId,
    });
    if (user.id) {
      //Interfaces...
      let intrfacesActive = [];
      let result = await this.context.api.q("/admin/member/interface", {
        id: this.props.userId,
      });
      if (result !== false) {
        if (result["id"]) result = [result];
        if (Array.isArray(result)) {
          result.forEach((item) => intrfacesActive.push(String(item.id)));
        }
      }

      let interfaces = {};
      result = await this.context.api.q("/admin/interface/list");
      if (result !== false) {
        if (result["id"]) result = [result];
        if (Array.isArray(result)) {
          result.forEach((item) => (interfaces[item.id] = item));
        }
      }

      //Groups...
      let groupsActive = [];
      result = await this.context.api.q("/admin/member/group", {
        id: this.props.userId,
      });
      if (result !== false) {
        if (result["id"]) result = [result];
        if (Array.isArray(result)) {
          result.forEach((item) => groupsActive.push(String(item.id)));
        }
      }

      let groups = {};
      result = await this.context.api.q("/admin/group/list");
      if (result !== false) {
        if (result["id"]) result = [result];
        if (Array.isArray(result)) {
          result.forEach((item) => (groups[item.id] = item));
        }
      }

      //Iptable...

      let ipAllow = [];
      result = await this.context.api.q("/admin/user/iptable/get", {
        id: this.props.userId,
        type: "A",
      });
      if (result !== false) {
        ipAllow = result["iptable"] ? result["iptable"].split(", ") : [];
      }

      let ipDeny = [];
      result = await this.context.api.q("/admin/user/iptable/get", {
        id: this.props.userId,
        type: "D",
      });
      if (result !== false) {
        ipDeny = result["iptable"] ? result["iptable"].split(", ") : [];
      }

      this.setState({
        user,
        intrfacesActive,
        interfaces,
        groupsActive,
        groups,
        ipAllow,
        ipDeny,
        notFound: false,
      });
    } else {
      this.setState({ user: {}, notFound: true });
    }
    this.pendingEnd();
  };

  onTabChange = (key) => {
    this.setState({ tabKey: key });
  };

  onInterfaceToggle = async (id, checked) => {
    const { intl } = this.props;
    this.pendingStart();

    let result = false;

    if (checked) {
      result = await this.context.api.q("/admin/member/interface/add", {
        interface: id,
        id: this.props.userId,
      });
    } else {
      result = await this.context.api.q("/admin/member/interface/delete", {
        interface: id,
        id: this.props.userId,
      });
    }
    if (result !== false) {
      message.success(intl.formatMessage({ id: "save.success" }));
      this.load();
    } else {
      message.error(intl.formatMessage({ id: "save.fail" }));
      this.pendingEnd();
    }
  };

  onGroupToggle = async (id, checked) => {
    const { intl } = this.props;
    this.pendingStart();

    let result = false;

    if (checked) {
      result = await this.context.api.q("/admin/member/group/add", {
        groupid: id,
        id: this.props.userId,
      });
    } else {
      result = await this.context.api.q("/admin/member/group/delete", {
        groupid: id,
        id: this.props.userId,
      });
    }
    if (result !== false) {
      message.success(intl.formatMessage({ id: "save.success" }));
      this.load();
    } else {
      message.error(intl.formatMessage({ id: "save.fail" }));
      this.pendingEnd();
    }
  };

  onIptableSave = async () => {
    const { intl } = this.props;
    this.pendingStart();
    let error = false;

    let result = await this.context.api.q("/admin/user/iptable/set", {
      id: this.props.userId,
      type: "A",
      iptable: this.state.ipAllow.join(","),
    });
    if (result === false) {
      error = true;
      message.error(intl.formatMessage({ id: "save.fail" }));
    }

    result = await this.context.api.q("/admin/user/iptable/set", {
      id: this.props.userId,
      type: "D",
      iptable: this.state.ipDeny.join(","),
    });
    if (result === false) {
      error = true;
      message.error(intl.formatMessage({ id: "save.fail" }));
    }

    if (!error) {
      message.success(intl.formatMessage({ id: "save.success" }));
      this.load();
    }
    this.pendingEnd();
  };

  onIpAdd = (type) => {
    let value = prompt(this.m("iptable.prompt"));

    if (value) {
      let arr = this.state[type];
      arr.push(value);
      this.setState({ [type]: arr });
    }
  };

  onIpRemove = (type, ip) => {
    let arr = this.state[type];
    let index = arr.indexOf(ip);
    if (index > -1) {
      arr.splice(index, 1);
      this.setState({ [type]: arr });
    }
  };

  onProfileSave = async (data) => {
    const { intl } = this.props;
    this.pendingStart();

    let saveData = {
      ...data,
      email: data.email,
      id: this.props.userId,
    };
    let result = await this.context.api.q("/admin/user/set", saveData);

    this.pendingEnd();
    if (result === false) {
      message.error(intl.formatMessage({ id: "save.fail" }));
    } else {
      message.success(intl.formatMessage({ id: "save.success" }));
      this.load();
    }
  };
  m = (key, prefix = "admin.users.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  render() {
    const { pending, user, notFound, tabKey } = this.state;
    const { intl } = this.props;
    if (notFound) {
      return (
        <Alert
          type="error"
          message={intl.formatMessage({ id: "nothing.found" })}
        />
      );
    }
    return (
      <>
        <Card>
          <Spin spinning={pending ? true : false}>
            <UserDetailTitle m={this.m} user={user} />
            <hr />
            <Tabs
              defaultActiveKey={tabKey}
              onChange={this.onTabChange}
              style={{ overflow: "visible" }}
            >
              <TabPane tab={this.m("tab.profile")} key="profile">
                <Form
                  key={user.id}
                  name="user"
                  layout="vertical"
                  initialValues={{
                    username: user.username || "",
                    email: user.email || "",
                    name: user.name || "",
                    phone: user.phone || "",
                    description: user.description || "",
                    passwordchange: user.passwordchange ? true : false,
                    passwordnotchange: user.passwordnotchange ? true : false,
                  }}
                  onFinish={this.onProfileSave}
                >
                  <Row gutter={[24, 8]} style={{ flexDirection: "row" }}>
                    <Col span={12}>
                      <Form.Item
                        rules={[{ required: true }]}
                        label={this.m("username")}
                        name="username"
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        rules={[{ required: false }]}
                        label={this.m("name")}
                        name="name"
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        rules={[
                          {
                            required: false,
                            type: "email",
                            message: this.m("signup.email.invalid", ""),
                          },
                        ]}
                        label={this.m("email")}
                        name="email"
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item label={this.m("phone")} name="phone">
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item label={this.m("password")} name="password">
                        <Input type="password" />
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        label={this.m("description")}
                        name="description"
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                    <Col span={24}>
                      <Form.Item name="passwordchange" valuePropName="checked">
                        <Checkbox>{this.m("passwordchange")}</Checkbox>
                      </Form.Item>
                      <Form.Item
                        name="passwordnotchange"
                        valuePropName="checked"
                      >
                        <Checkbox>{this.m("passwordnotchange")}</Checkbox>
                      </Form.Item>
                    </Col>
                  </Row>

                  <Form.Item style={{ marginTop: "20px" }}>
                    <Space>
                      <Button
                        loading={pending}
                        type="primary"
                        htmlType="submit"
                      >
                        {intl.formatMessage({
                          id: "save",
                        })}
                      </Button>
                    </Space>
                  </Form.Item>
                </Form>
              </TabPane>
              <TabPane tab={this.m("tab.groups")} key="groups">
                <Space direction="vertical">
                  {Object.entries(this.state.groups).map(([id, item]) => (
                    <div key={id}>
                      <Checkbox
                        onChange={(e) =>
                          this.onGroupToggle(id, e.target.checked)
                        }
                        checked={
                          this.state.groupsActive.includes(id) ? true : false
                        }
                      >
                        {item.name}
                      </Checkbox>
                    </div>
                  ))}
                </Space>
              </TabPane>
              <TabPane tab={this.m("tab.ip")} key="ip">
                <Row gutter={16}>
                  <Col span={12}>
                    <Space direction="vertical">
                      <Flex between>
                        {this.m("iptable.allowed")}
                        <Button
                          className="gx-mb-0"
                          onClick={() => this.onIpAdd("ipAllow")}
                          icon={<PlusOutlined />}
                        />
                      </Flex>
                      <hr />
                      {this.state.ipAllow.length === 0 && (
                        <Text type="secondary">{this.m("iptable.none")}</Text>
                      )}
                      {this.state.ipAllow.map((ip) => (
                        <div key={ip}>
                          <Space>
                            {ip}
                            <DeleteOutlined
                              onClick={() => this.onIpRemove("ipAllow", ip)}
                            />
                          </Space>
                        </div>
                      ))}
                    </Space>
                  </Col>
                  <Col span={12}>
                    <Space direction="vertical">
                      <Flex between>
                        {this.m("iptable.denied")}
                        <Button
                          className="gx-mb-0"
                          onClick={() => this.onIpAdd("ipDeny")}
                          icon={<PlusOutlined />}
                        />
                      </Flex>
                      <hr />
                      {this.state.ipDeny.length === 0 && (
                        <Text type="secondary">{this.m("iptable.none")}</Text>
                      )}
                      {this.state.ipDeny.map((ip) => (
                        <div key={ip}>
                          <Space>
                            {ip}
                            <DeleteOutlined
                              onClick={() => this.onIpRemove("ipDeny", ip)}
                            />
                          </Space>
                        </div>
                      ))}
                    </Space>
                  </Col>
                </Row>
                <div>
                  <br />
                </div>
                <Button type="primary" onClick={this.onIptableSave}>
                  {intl.formatMessage({ id: "save" })}
                </Button>
              </TabPane>
              <TabPane tab={this.m("tab.interfaces")} key="interfaces">
                <Space direction="vertical">
                  {Object.entries(this.state.interfaces).map(([id, item]) => (
                    <div key={id}>
                      <Checkbox
                        onChange={(e) =>
                          this.onInterfaceToggle(id, e.target.checked)
                        }
                        checked={
                          this.state.intrfacesActive.includes(id) ? true : false
                        }
                      >
                        {item.name}
                      </Checkbox>
                    </div>
                  ))}
                </Space>
              </TabPane>
            </Tabs>
          </Spin>
        </Card>
      </>
    );
  }
}

export default withRouter(injectIntl(UsersDetail));
