import React from "react";
import { injectIntl } from "react-intl";
import { Link, withRouter } from "react-router-dom";
import key from "../../../util/key";
import DataTable from "../../../components/Datatable/DataTable";
import DataTablePeriod from "../../../components/Datatable/DataTablePeriod";
import DataTableSearch from "../../../components/Datatable/DataTableSearch";
import Flex from "../../../util/Flex";
import {
  Form,
  Input,
  Button,
  Popconfirm,
  Space,
  Tooltip,
  message,
  Tag,
  Modal,
  Spin,
} from "antd";
import Context from "../../../Context";
import paramsHasChanged from "../../../util/paramsHasChanged";
import {
  PlusOutlined,
  DeleteOutlined,
  PoweroffOutlined,
  SearchOutlined,
  EditOutlined,
  UserOutlined,
} from "@ant-design/icons";

import UsersDetail from "./UsersDetail";
import { Helmet } from "react-helmet";
import Title from "../Title";
const { TextArea } = Input;

class Users extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      treeSearch: "",
      listKey: key(),
      tree: [],
      columns: [],
      item: {},
    };
  }

  pendingStart = () => {
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    this.setState({ pending: false });
  };

  componentDidMount() {
    this.setColumns();
    this.setParams();
    this.load();
  }

  componentDidUpdate(prevProps) {
    if (paramsHasChanged(prevProps, this.props)) {
      this.setParams();
    }

    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
      this.load();
    }
  }

  setParams = () => {
    const { param1: userId } = this.props.match.params;
    this.setState(
      {
        userId,
      },
      () => this.load()
    );
  };

  setColumns = () => {
    const { intl } = this.props;
    const labelColors = {
      open: "blue",
      active: "green",
      locked: "orange",
    };
    this.setState({
      columns: [
        {
          text: true,
          title: this.m("name"),
          dataIndex: "name",
          sorter: true,
          render: (value) => (
            <div style={{ whiteSpace: "nowrap" }}>{value}</div>
          ),

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },
        {
          text: true,
          title: this.m("username"),
          dataIndex: "username",
          sorter: true,
          render: (value) => (
            <div style={{ whiteSpace: "nowrap" }}>{value}</div>
          ),

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },
        {
          text: true,
          title: this.m("email"),
          dataIndex: "email",
          sorter: true,
          render: (value) => (
            <div style={{ whiteSpace: "nowrap" }}>{value}</div>
          ),

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },
        {
          width: "1%",
          title: this.m("created"),
          dataIndex: "created",
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br />
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          width: "1%",
          title: (
            <Flex between>
              <div>{this.m("statustext")}</div>
              <Space>
                <Button
                  onClick={() => this.onAddStart()}
                  icon={<PlusOutlined />}
                />
              </Space>
            </Flex>
          ),
          dataIndex: "statustext",
          render: (value, item) => {
            if (value) {
              return (
                <Flex between>
                  <Tag color={labelColors[value]}>{value}</Tag>
                  <div>
                    <Space>
                      <Link to={`/admin/users/${item.id}/`}>
                        <Button icon={<EditOutlined />} />
                      </Link>

                      <Tooltip title={this.m("btn.block")}>
                        <Button
                          onClick={() => this.onBlockToggle(item)}
                          icon={<PoweroffOutlined />}
                        />
                      </Tooltip>

                      <Popconfirm
                        title={`${intl.formatMessage({
                          id: "delete.sure",
                        })}`}
                        onConfirm={() => this.onDelete(item.id)}
                        okText={intl.formatMessage({ id: "yes" })}
                        cancelText={intl.formatMessage({ id: "no" })}
                      >
                        <Tooltip title={this.m("btn.delete")}>
                          <Button icon={<DeleteOutlined />} />
                        </Tooltip>
                      </Popconfirm>
                    </Space>
                  </div>
                </Flex>
              );
            }
          },
        },
      ],
    });
  };

  load = async () => {};

  onAdd = async (data) => {
    const { intl } = this.props;
    this.pendingStart();

    let saveData = {
      ...data,
      passwordchange: false,
      passwordnotchange: false,
    };
    let result = await this.context.api.q("/admin/user/set", saveData);

    this.pendingEnd();
    if (result === false) {
      message.error(intl.formatMessage({ id: "save.fail" }));
    } else {
      message.success(intl.formatMessage({ id: "save.success" }));
      this.props.history.push(`/admin/users/${result.id}/`);
    }
  };

  onBlockToggle = async (item) => {
    const { intl } = this.props;
    const { id, lock_date } = item;
    let action = lock_date ? "unlock" : "lock";
    let result = await this.context.api.q("/admin/user/" + action, {
      id,
    });
    if (result === false) {
      message.error(intl.formatMessage({ id: "save.fail" }));
    } else {
      message.success(intl.formatMessage({ id: "save.success" }));
      this.refreshList();
    }
  };

  onAddStart = () => {
    this.props.history.push(`/admin/users/add`);
  };

  onModalClose = () => {
    this.props.history.push(`/admin/users/`);
  };

  refreshList = () => {
    this.setState({ listKey: key() });
  };

  onDelete = async (userId) => {
    const { intl, history } = this.props;
    this.pendingStart();

    let result = await this.context.api.q("/admin/user/delete", {
      id: userId,
    });
    if (result !== false) {
      message.success(intl.formatMessage({ id: "delete.success" }));
      history.push(`/admin/users/`);
      this.refreshList();
    } else {
      message.error(intl.formatMessage({ id: "delete.fail" }));
      this.pendingEnd();
    }
  };

  m = (key, prefix = "admin.users.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  render() {
    const { pending, userId, item } = this.state;
    const { intl } = this.props;

    if (userId && userId !== "add") {
      return (
        <>
          <Title
            title={this.m("users")}
            url="/admin/users"
            icon={<UserOutlined />}
          />
          <UsersDetail userId={userId} />
        </>
      );
    }
    return (
      <>
        <Title
          title={this.m("users")}
          url="/admin/users"
          icon={<UserOutlined />}
        />
        <DataTable
          uuid={this.state.listKey}
          columns={this.state.columns}
          path={"/admin/user/list"}
        />
        <Modal
          onCancel={this.onModalClose}
          visible={userId === "add"}
          footer={null}
        >
          <Spin spinning={pending ? true : false}>
            <Form
              key={item.id}
              layout="vertical"
              name="user"
              initialValues={{
                username: "",
                email: "",
                name: "",
                phone: "",
                description: "",
              }}
              onFinish={this.onAdd}
            >
              <Space direction="vertical" style={{ width: "100%" }}>
                <Form.Item
                  rules={[{ required: true }]}
                  label={this.m("username")}
                  name="username"
                >
                  <Input />
                </Form.Item>
                <Form.Item label={this.m("name")} name="name">
                  <Input />
                </Form.Item>
                <Form.Item label={this.m("email")} name="email">
                  <Input />
                </Form.Item>
                <Form.Item label={this.m("phone")} name="phone">
                  <Input />
                </Form.Item>

                <Form.Item label={this.m("description")} name="description">
                  <TextArea />
                </Form.Item>

                <Form.Item style={{ marginTop: "20px" }}>
                  <Space>
                    <Button loading={pending} type="primary" htmlType="submit">
                      {intl.formatMessage({
                        id: "save",
                      })}
                    </Button>
                    <Button onClick={this.onModalClose}>
                      {intl.formatMessage({
                        id: "cancell",
                      })}
                    </Button>
                  </Space>
                </Form.Item>
              </Space>
            </Form>
          </Spin>
        </Modal>
      </>
    );
  }
}

export default withRouter(injectIntl(Users));
