import React from "react";
import { injectIntl } from "react-intl";
import { Space, Tooltip, Typography } from "antd";
import Context from "../../../Context";
import Flex from "../../../util/Flex";
import {
  PlusCircleOutlined,
  RedoOutlined,
  CloseCircleOutlined,
} from "@ant-design/icons";

const { Text } = Typography;

class UsersDetailTitle extends React.Component {
  static contextType = Context;

  render() {
    const { intl, user, m } = this.props;

    return (
      <>
        <Flex between>
          <Tooltip title={m("title.name")}>
            <Space>
              <h3>{user.name || user.username}</h3>
            </Space>
          </Tooltip>
          <Space>
            <Tooltip title={m("title.created")}>
              <Space>
                <PlusCircleOutlined />
                <Text type="secondary">
                  {intl.formatDate(user.created)}{" "}
                  {intl.formatTime(user.created)}
                </Text>
              </Space>
            </Tooltip>
            {user.input_last && (
              <Tooltip title={m("title.input_last")}>
                <Space>
                  <RedoOutlined />
                  <Text type="secondary">
                    {intl.formatDate(user.input_last)}{" "}
                    {intl.formatTime(user.input_last)}
                  </Text>
                </Space>
              </Tooltip>
            )}
            {user.lock_date && (
              <Tooltip title={m("title.lock_date")}>
                <Space>
                  <Text type="danger">
                    <CloseCircleOutlined />
                  </Text>
                  <Text type="danger">
                    {intl.formatDate(user.lock_date)}{" "}
                    {intl.formatTime(user.lock_date)}
                  </Text>
                </Space>
              </Tooltip>
            )}
          </Space>
        </Flex>
      </>
    );
  }
}

export default injectIntl(UsersDetailTitle);
